
/* 'Nunito Sans' normal 200*/
@font-face {
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 200;
    src:
    url('../fonts/nunito-sans-normal-200.woff2') format('woff2'),
    url('../fonts/nunito-sans-normal-200.woff') format('woff');
}

/* 'Nunito Sans' italic 200*/
@font-face {
    font-family: 'Nunito Sans';
    font-style: italic;
    font-weight: 200;
    src:
    url('../fonts/nunito-sans-italic-200.woff2') format('woff2'),
    url('../fonts/nunito-sans-italic-200.woff') format('woff');
}

